<template>
    <div>
        <div class="header" v-if="FlagSeek">
            <div slot="left"> <van-icon name="arrow-left" @click="GoBack()"/></div>
            <div slot="center"><h3>搜索</h3></div>
        </div> 

        <div class="header" v-if="FlagLogin">
            <div slot="left"> <van-icon name="arrow-left" @click="GoBack()"/></div>
            <div slot="center"><h3>密码登录</h3></div>
        </div>

        <div class="header" v-if="FlagMine">
            <div slot="left"> <van-icon name="arrow-left" @click="GoBack()"/></div>
            <div slot="center"><h3>我的</h3></div>
        </div>

        <div class="header" v-if="FlagIndent">
            <div slot="left"> <van-icon name="arrow-left" @click="GoBack()"/></div>
            <div slot="center"><h3>订单列表</h3></div>
        </div>

         <div class="header" v-if="FlagInfo">
            <div slot="left"> <van-icon name="arrow-left" @click="GoBack()"/></div>
            <div slot="center"><h3>账户信息</h3></div>
         </div>
         <div class="header" v-if="FlagAddress">
            <div slot="left"> <van-icon name="arrow-left" @click="GoBack()"/></div>
            <div slot="center"><h3>编辑地址</h3></div>
            <div slot="right" ><span  @click="bian()">{{this.ClickChange}}</span></div>
         </div>
         <div class="header" v-if="FlagAdd">
            <div slot="left"> <van-icon name="arrow-left" @click="GoBack()"/></div>
            <div slot="center"><h3>新增地址</h3></div>
         </div>
         <div class="header" v-if="FlagAddDetail">
            <div slot="left"> <van-icon name="arrow-left" @click="GoBack()"/></div>
            <div slot="center"><h3>搜索地址</h3></div>
         </div>
    </div>
</template>

<script>
function rem(){
    document.documentElement.style.fontSize = document.documentElement.clientWidth / 4.5 + "px";
}
rem();
window.onresize = rem;
    export default {
        props:["FlagSeek","FlagLogin","FlagMine","FlagIndent","FlagInfo","FlagAddress","FlagAdd","FlagAddDetail"],
        data(){
            return{
                update:false,
                ClickChange:'编辑'
            }
        },
        methods: {
            //返回上次路由
            GoBack() {
            this.$router.go(-1);
            },
            bian(){
                if(this.ClickChange=='编辑'){
                    this.ClickChange='完成'
                    this.update=true
                }else{
                    this.ClickChange='编辑'
                    this.update=false                    
                }
                this.$emit('dd',this.update)
            },
        },
        mounted() {
             this.update=true
        },
           
        

    }
</script>

<style lang="scss" scoped>

 .header {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 0.6rem;
  background: #3190e8;
  display: flex;
  line-height: 0.6rem;
}
 .header span{
      margin-left:1rem;
      color: #fff;
      font-size: 0.22rem;
  }
.header  .van-icon {
  font-size: 0.25rem;
  margin-top: 0.15rem;
  margin-left: 0.1rem;
  margin-right: 0.6rem;
  color: #fff;
}
.header h3 {
  color: #fff;
  margin-left: 1rem;
}
</style>