<template>
    <div>
        <Header :FlagInfo=true ></Header>
        <div class="head">
            <input type="file" @change="avatar()" ref="inp">
            <span>头像</span>
            <div>
                <img :src="`//elm.cangdu.org/img/`+this.$store.state.avaimg" alt="" v-if="this.$store.state.avaimg">
                <img :src="`//elm.cangdu.org/img/${admin.avatar}`" alt="" v-else>
                <span>
                    <van-icon name="arrow" />
                </span>
                
            </div>
        </div>
        <div class="nav">
            <span>用户名</span>
            <div>
                <span>{{username.username}}</span>
                <span>
                    <van-icon name="arrow" />
                </span>
            </div>
        </div>
        <router-link to="/address">
        <div class="shou">
            <span>收货地址</span>
            <div>
                <span>
                    <van-icon name="arrow" />
                </span>
            </div>
        </div>
        </router-link>
        <div class="dd">
            账号绑定
        </div>
        <div class="jj">
            <span>
                <img src="" alt="">
                手机
            </span>
            <div>
                <span>
                    <van-icon name="arrow" />
                </span>
            </div>
        </div>
        <div class="dd">
            安全设置
        </div>
        <div class="login">
            <span>
                登录密码
            </span>
            <div>
                <span>修改</span>
                <span>
                    <van-icon name="arrow" />
                </span>
            </div>
        </div>
        <div class="btn">
            <van-button type="danger" @click="tui()">退出登录</van-button>
        </div>
    </div>
</template>

<script>
    import Header from "./../Unify/Header"
    import {Dialog} from "vant"
    export default {
        data(){
            return{
                admin:"",
                username:""
            }
        },
        components:{
          Header
          
        },
        mounted() {
            this.$http.get("http://elm.cangdu.org/v1/user?user_id=38296").then((res)=>{
                // console.log(res);
                this.admin=res.data;
                this.username=this.$store.state.ma;
        //   console.log(this.username);

            })
        },
        methods: {
            tui(){
                this.$http.get("http://elm.cangdu.org/v2/signout").then((res)=>{
                    // console.log(res) 
                    Dialog.confirm({
                    title: '标题',
                    message: '弹窗内容'
                    }).then(() => {
                        this.$store.commit("tui",res.data);
                        this.$router.push('/mine')
                    }).catch(() => {
                });
                })
               
            },
            avatar(){
                let input=this.$refs.inp;
                let data=new FormData;
                data.append("file",input.files[0])
                var user_id=this.$store.state.ma.user_id
                this.$http.post("http://elm.cangdu.org/eus/v1/users/"+user_id+"/avatar",data,
                {headers:{"Content-Type":"multipart/form-data"}}
                ).then((res)=>{
                    console.log(res);
                    this.$store.commit("avatar",res.data.image_path)
                })
            }
        },
    }
</script>

